*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input {
  font: inherit;
}

button {
  cursor: pointer;
  margin: 4px;
  padding: 8px 12px;
  font-family: inherit;
  font-size: 16px;
}

.hidden {
  display: none;
}

.back-btn {
  text-transform: uppercase;
  color: #fff;
  background-color: #f68768;
  border: none;
  border-radius: .5rem;
  font-size: 1rem;
  font-weight: bold;
  text-decoration: none;
  transition: transform .2s ease-out, box-shadow .2s ease-out;
  box-shadow: 0 2px 5px #d32525b3;
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px #169bafb3;
}

.back-btn:active {
  box-shadow: none;
  transform: translateY(0);
}

.back-btn__link {
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
  color: inherit;
  align-items: center;
  padding: .7rem 1.5rem;
  display: flex;
}

.back-btn .icon {
  width: 1.5rem;
  height: 1.5rem;
  fill: inherit;
}

.magic-btn {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  background-color: #0bcc95;
  border: none;
  border-radius: 8px;
  padding: 15px 30px;
  font-size: 20px;
  transition: transform .3s;
  display: block;
  overflow: hidden;
  box-shadow: 0 5px #ef8d8f;
}

.magic-btn:hover {
  transform: translateY(5px);
  box-shadow: 0 2px #f08d8e;
}

.magic-btn:before, .magic-btn:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(45deg, #c81c2db3 0%, #0000 80%);
  transition: transform .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.magic-btn:before {
  transform: rotateX(90deg)translateZ(-40px);
}

.magic-btn:after {
  transform: rotateY(90deg)translateZ(-40px);
}

.magic-btn:hover:before {
  transform: rotateX(0) translateZ(0);
}

.magic-btn:hover:after {
  transform: rotateY(0) translateZ(0);
}

.magic-btn > .icon {
  margin-left: 10px;
  font-size: 24px;
  display: inline-block;
}

.page-header {
  color: #333;
  text-shadow: 0 2px 2px #0000001a;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 2rem;
  font-family: Poppins, sans-serif;
  font-size: 3.5rem;
  font-weight: 600;
}

.page-header:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #5f5f5f;
  margin-top: 1.5rem;
  display: block;
  box-shadow: 0 2px 2px #0000001a;
}

.hero {
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  display: flex;
}

.materials__wrap {
  max-width: 30rem;
  padding: 1.6rem;
}

.materials__header {
  margin-bottom: 1rem;
  padding-left: 2rem;
  font-size: 2rem;
  font-weight: bold;
}

.materials__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.materials__item {
  margin-bottom: .5rem;
}

.materials__link {
  color: #333;
  padding: .5rem;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all .2s ease-in-out;
  display: block;
  box-shadow: 2px 2px 5px #0000004d;
}

.materials__link:hover {
  color: #4ae4c2;
  background-image: linear-gradient(to right, #fc354c, #0abfbc);
  transform: translateX(10px);
  box-shadow: 4px 4px 10px #00000080;
}

.footer {
  color: #333;
  text-align: center;
  margin: 2rem 0;
  font-size: .8rem;
}

.footer:before {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #5f5f5f;
  margin-bottom: 3rem;
  display: block;
  box-shadow: 0 2px 2px #0000001a;
}

.footer__container {
  padding: 0 1rem;
}

.social__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  padding: 0;
  list-style: none;
  display: flex;
}

.social__item {
  width: 2.75rem;
  height: 2.75rem;
}

.social__link {
  color: #fff;
  width: 100%;
  height: 100%;
  background-color: #ffffff4d;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.social__link:hover, .social__link:focus {
  color: #fff;
  background-image: linear-gradient(to right, #0abfbc, #fc354c);
  transform: scale(1.2);
  box-shadow: 4px 4px 10px #00000080;
}

.social__icon {
  fill: currentColor;
