  height: 45%;
}

.to-top-btn {
  width: 2.75rem;
  height: 2.75rem;
  color: #fff;
  cursor: pointer;
  background-color: #ffffff4d;
  border: none;
  border-radius: 50%;
  transition: background-color .3s ease-in-out, color .3s ease-in-out;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}

.to-top-btn:hover {
  background-image: linear-gradient(to right, #0abfbc, #fc354c);
}

.to-top-btn__icon {
  width: 100%;
  height: 100%;
  fill: currentColor;
  transition: transform .3s ease-in-out;
}

.to-top-btn:hover .to-top-btn__icon {
  transform: scale(2);
}

.counter {
  align-items: center;
  display: flex;
}

.counter > p {
  margin: 0;
  font-family: monospace;
  font-size: 48px;
}

.site-nav_link {
  color: currentColor;
  text-decoration: none;
}

.site-nav_link:hover {
  text-decoration: underline;
}

.site-nav__link--current {
  color: tomato;
}

.color-picker__option {
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: none;
  outline: none;
  margin: 4px;
